import React from 'react'
import Styles from 'styled-components';
import Input_holder from './Input_holder';
import 'bootstrap/dist/css/bootstrap.css';

function Box_holder() {
    return (
        <div>
            <Form_title>Login Page</Form_title>
            <Form_box>
                
            <Input_holder  name="Enter Username" /> 

            <Input_holder name="Enter Password" /> 
            <button className="btn btn-lg btn-primary mt-3 d-flex justify-content-start ml-4"   style={{marginLeft:40}}>Submit</button> 
            </Form_box>
        </div>
    )
}



const Form_title = Styles.div`
width:300px;
height:40px;
align-text:center;
font-size:30px;
font-weight:bold;
margin-top:20px;

`
const Form_box = Styles.div`
    width:400px;
    height:290px;
    // box-shadow: 2px 2px 7px gray;
    margin-top:20px;
    border:0.5px solid silver;
    border-radius:5px;
`


export default Box_holder
